<template>
    <div id="app">
        <div style="
        border-bottom: 1px solid #eee;
        margin-bottom: 12px;
        box-shadow: 1px 0px 10px #ccc;
      ">
            <van-nav-bar title="问卷调查" />
        </div>
        <van-form>
            <div class="item-wrap" v-for="(item, index) in dataList" :key="index">
                <div v-if="item.IsMultiSelect == 0">
                    <div class="title">
                        <span>{{ index + 1 }}.{{ item.Title }}</span>
                    </div>
                    <div class="content">
                        <van-field name="radio">
                            <template #input>
                                <van-radio-group v-model="item.checkedId">
                                    <van-radio v-for="(it, index) in item.QuestionOptionList" :key="index" :name="it.Id" @click="changeColor(it.name)" :class="tagColor === it.name ? 'bordercolor' : ''" :disabled="isVote">{{ it.Code }}</van-radio>
                                </van-radio-group>
                            </template>
                        </van-field>
                    </div>
                </div>

                <div v-else-if="item.IsMultiSelect == 1">
                    <div class="title">
                        <span> {{ index + 1 }}.{{ item.Title }} </span>
                    </div>
                    <div class="content">
                        <van-field name="checkboxGroup">
                            <template #input>
                                <van-checkbox-group v-model="item.checkedId" :disabled="isVote">
                                    <van-checkbox v-for="(it, index) in item.QuestionOptionList" :key="index" :name="it.Id" shape="square" :class="
                      result.indexOf(it.name) === -1 ? '1' : 'bordercolor'
                    ">{{ it.Code }}</van-checkbox>
                                </van-checkbox-group>
                            </template>
                        </van-field>
                    </div>
                </div>
            </div>
            <div style="margin: 18px 30px">
                <van-button round block type="info" @click="submit" v-if="!isVote">提交</van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            radio: 1,
            result: [],
            message: "",
            tagColor: 0,
            dataList: [],
            isVote: false,
            Questionnaire: []
        };
    },

    methods: {
        changeColor(radio) {
            if (radio) {
                this.tagColor = radio;
            }
        },
        QSMS_QuestionnaireDetails() {
            this._api
                .QSMS_QuestionnaireDetails({
                    Id: this.$route.query.QuestionnaireId
                })
                .then((res) => {
                    if (res) {
                        if (res.data && res.data.QuestionList) {
                            this.dataList = res.data.QuestionList;
                            this.Questionnaire = res.data.Questionnaire
                            if (res.data.Questionnaire.KpsList.length) {
                                this.$u.confirm("评测结果不保存，如需保存请自行截图！").then(() => { }).catch(() => { })
                            }
                            this.QSMS_QuestionnairePersoniDetails(
                                this.$route.query.QuestionnaireId
                            );
                        }
                    }
                });
        },
        QSMS_QuestionnairePersoniDetails(Id) {
            this._api
                .QSMS_QuestionnairePersoniDetails({
                    questionnaireId: Id,
                })
                .then((res) => {
                    if (res) {
                        if (!res.data || res.data.VotingRecordList.length == 0) {
                            this.isVote = false;
                        } else {
                            this.isVote = true;
                            // 还原

                            for (let it of res.data.VotingRecordList) {
                                for (let item of this.dataList) {
                                    if (item.Id == it.QuestionId) {
                                        if (!item.checkedId) item.checkedId = [];
                                        item.checkedId.push(it.OptionId);
                                    }
                                }
                            }

                            for (let item of this.dataList) {
                                if (item.checkedId.length == 1)
                                    item.checkedId = item.checkedId.join(",");
                            }
                            let data = JSON.parse(JSON.stringify(this.dataList));
                            this.dataList = [];
                            setTimeout(() => {
                                this.dataList = data;
                            });
                        }
                    }
                    this.loading = false;
                });
        },
        submit() {
            let myInfo = JSON.parse(localStorage.getItem("myInfo"));
            let ModuleId = "5c4edc3a-b094-4656-894a-faff28b684a1";
            let submitData = {
                ModuleId: ModuleId,
                QuestionnaireId: this.$route.query.QuestionnaireId,
                PsersonId: myInfo.Id,
                MemberId: myInfo.Id,
                VotingRecordList: [],
            };

            let flag = true;
            for (let item of this.dataList) {
                if (item.IsMultiSelect) {
                    for (let itemer of item.checkedId) {
                        for (let it of item.QuestionOptionList) {
                            if (it.Id == itemer) {
                                let obj = {
                                    QuestionId: item.Id,
                                    QuestionnaireId: this.QuestionnaireId,
                                    PsersonId: myInfo.Id,
                                    MemberId: myInfo.Id,
                                    ModuleId: ModuleId,
                                    OptionId: it.Id,
                                    Avalue: it.Avalue,
                                    FValue: it.FValue
                                };
                                submitData.VotingRecordList.push(obj);
                            }
                        }
                    }
                } else {
                    if (item.checkedId) {
                        for (let it of item.QuestionOptionList) {
                            if (it.Id == item.checkedId) {
                                let obj = {
                                    QuestionId: item.Id,
                                    QuestionnaireId: this.QuestionnaireId,
                                    PsersonId: myInfo.Id,
                                    MemberId: myInfo.Id,
                                    ModuleId: ModuleId,
                                    OptionId: it.Id,
                                    Avalue: it.Avalue,
                                    FValue: it.FValue
                                };
                                submitData.VotingRecordList.push(obj);
                            }
                        }
                    } else {
                        flag = false;
                    }
                }
            }

            if (flag == false) {
                this.$u.msg("投票失败！投票未完成！");
                return;
            }

            if (this.Questionnaire.KpsList.length !== 0) {
                let sum = submitData.VotingRecordList.reduce((preV, nV) => {
                    return preV + nV.FValue
                }, 0)

                for (let item of this.Questionnaire.KpsList) {
                    if (item.MinValue > sum && sum < item.MaxValue) {
                        this.$u.confirm(item.Description, "", "确认", `${item.Summary}(${sum}分)`, false).then(() => {
                            this.$router.back()
                        }).catch(() => { })
                        break
                    }
                }

                return
            }


            this._api.QSMS_QuestionnairePersonVoting(submitData).then((res) => {
                if (res.errcode == 0) {
                    this.$u.msg("投票成功！");
                    this.$router.back();
                } else {
                    this.$u.msg("发生错误！投票失败！", "error");
                }
            });
        },
    },

    mounted() {
        if (this.$route.query.QuestionnaireId) {
            let nowT = new Date().getTime();
            if (nowT > this.$route.query.StartTime)
                return this.$u.msg("活动已过期！", "/static/img/warning.png");
            this.QSMS_QuestionnaireDetails();

        }
    },
};
</script>

<style lang="scss">
#app {
    overflow-y: hidden;
    height: 100vh;

    .item-wrap {
        width: 343px;
        height: 263px;
        background: #ffffff;
        box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.12),
            0px 4px 5px 0px rgba(0, 0, 0, 0.08),
            0px 1px 10px 0px rgba(0, 0, 0, 0.05);
        border-radius: 4px;
        margin: 0 auto;
        margin-bottom: 16px;

        .title {
            width: 100%;
            padding: 12px 16px;
            box-sizing: border-box;
            border-bottom: 1px solid #eee;
            position: relative;

            span {
                font-size: 16px;
                font-family: PingFang SC-Bold, PingFang SC;
                font-weight: bold;
                color: rgba(0, 0, 0, 0.9);
                line-height: 24px;
            }

            .required::after {
                color: #ee0a24;
                font-size: 14px;
                content: "*";
                position: relative;
                left: -8px;
            }
        }

        .content {
            .fill {
                display: flex;
                justify-content: space-around;
                padding: 5px 0;

                .head {
                    font-size: 16px;
                    font-family: PingFang SC-Regular, PingFang SC;
                    font-weight: 400;
                    color: #000000;
                    line-height: 24px;
                }

                input {
                    border: none;
                    font-size: 16px;
                    font-family: PingFang SC-Regular, PingFang SC;
                    font-weight: 400;
                    color: rgba(0, 0, 0, 0.6);
                    line-height: 24px;
                }
            }

            textarea {
                border: #666;
            }
        }
    }
}

textarea {
    font-size: 14px;
    font-family: PingFang SC-Regular, PingFang SC;
    font-weight: 400;
    color: rgba(224, 221, 221, 0.15);
    line-height: 22px;
}

.van-radio,
.van-checkbox {
    width: 246px !important;
    height: 30px !important;
    background: #ffffff !important;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.12),
        0px 4px 5px 0px rgba(0, 0, 0, 0.08),
        0px 1px 10px 0px rgba(0, 0, 0, 0.05) !important;
    border-radius: 3px !important;
    opacity: 1;
    box-sizing: content-box !important;
    padding: 0px 6px !important;
    margin-bottom: 8px !important;
    border: 1px solid #eee;
}

.van-radio__label,
.van-checkbox__label {
    font-size: 14px !important;
    font-family: PingFang SC-Regular, PingFang SC !important;
    font-weight: 400 !important;
    color: #000000 !important;
    line-height: 22px !important;
    margin-left: 10px !important;
    white-space: nowrap;
}

.van-nav-bar__left {
    padding: 0 16px 0 0 !important;
    color: #000;
}

.van-icon-arrow-left:before {
    color: #000 !important;
}

.van-nav-bar__content {
    width: 85%;
    margin: 0 auto;
    line-height: 50px;
}

.van-hairline--bottom::after {
    border-bottom-width: 0px !important;
}

.textarea .van-field {
    border: 1px solid #eee;
}

.van-field__label {
    font-size: 16px;
    font-family: PingFang SC-Regular, PingFang SC;
    font-weight: 400;
    color: #000000;
    line-height: 24px;
}

.van-field__word-limit {
    font-size: 16px !important;
    font-family: PingFang SC-Regular, PingFang SC !important;
    font-weight: 400 !important;
    color: rgba(0, 0, 0, 0.3) !important;
}

.van-cell__value.van-cell__value--alone.van-field__value {
    border: 1px solid #eee;
    padding: 6px;
}

.bordercolor {
    border: 1px solid rgba(0, 122, 255, 0.8) !important;
}

.van-cell__value.van-cell__value--alone.van-field__value {
    padding: 6px 8px 0px 8px !important;
}

.van-form {
    height: calc(100vh - 46px) !important;
    width: 100vw !important;
    overflow-x: hidden;
    overflow-y: auto;
    margin-bottom: 20px;
}

.van-checkbox__icon--checked .van-icon-success,
.van-radio__icon--checked .van-icon-success {
    background-color: #1989fa !important;
    border-color: #1989fa !important;
    color: #fff !important;
}
</style>
